import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Loading, AlertController, LoadingController } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { StripeService, Elements, Element as StripeElement } from "ngx-stripe";
import { ToolsProvider } from '../../providers/tools/tools';
import { CheckinServiceProvider } from '../../providers/checkin-service/checkin-service';
import { CheckoutServiceProvider } from '../../providers/checkout-service/checkout-service';
import { NativeStorage } from '@ionic-native/native-storage';
import { ReservationServiceProvider } from '../../providers/reservation-service/reservation-service';
import { HomePage } from '../../pages/home/home';
import { CommonServiceProvider } from '../../providers/common-service/common-service';
import { NotificationProvider } from '../../providers/notification/notification';

@IonicPage()
@Component({
  selector: 'page-checkout-payment',
  templateUrl: 'checkout-payment.html',
})
export class CheckoutPaymentPage {
  paymentDataForm: FormGroup;
  elements: Elements;
  card: StripeElement;
  loading: Loading;
  isCheckedAgree = false;
  storedCheckinDetails: any;
  buttonEnable = true;

  constructor(public navCtrl: NavController, public navParams: NavParams, public formBuilder: FormBuilder,
    private stripeService: StripeService, public toolsProvider: ToolsProvider,
    public alertCtrl: AlertController, public loadingCtrl: LoadingController,
    public checkinProvider: CheckinServiceProvider, private checkoutProvider: CheckoutServiceProvider,
    private nativeStorage: NativeStorage, public reservation: ReservationServiceProvider,
    public commonProvider: CommonServiceProvider, private notificationProvider: NotificationProvider) {
    this.paymentDataForm = this.formBuilder.group({
      'cardHolder': new FormControl(null, [Validators.required])
    });
    this.getStoredCheckinDetails();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CheckoutPaymentPage');

    this.stripeService.elements()
      .subscribe(elements => {
        this.elements = elements;
        // Only mount the element the first time
        if (!this.card) {
          this.card = this.elements.create('card', {
            style: {
              base: {
                iconColor: '#666EE8',
                color: '#31325F',
                lineHeight: '40px',
                fontWeight: 300,
                fontFamily: '"Helvetica Neue", Helvetica, sans-serif',
                fontSize: '15px',
                '::placeholder': {
                  color: '#CFD7E0'
                }
              }
            }
          });
          this.card.mount('#checkoutcard-element');
        }
      });

    this.fnCheckPreEnteredFormData();
  }

  //function to execute on button 'continue' click
  fnContinue() {
    //checking card holder validation at the success of card number validation
    if (this.paymentDataForm.controls["cardHolder"].value == null ||
      this.paymentDataForm.controls["cardHolder"].value == "") {
      let msg = this.toolsProvider.fnLanguageTranslate(["common.error", "view.reservation.label.cardHolderEmpty", "buttons.ok"]); // translate of error message
      msg.subscribe((value: string) => {
        let alertnew = this.alertCtrl.create({
          title: value['common.error'],
          message: value["view.reservation.label.cardHolderEmpty"],
          cssClass: 'cls_alrt',
          buttons: [
            {
              text: value['buttons.ok'],
              role: 'cancel'
            }]
        });
        alertnew.present();
        setTimeout(() => { alertnew.dismiss(); }, 5000);
      });
    }
    //checking if agreed to terms at the success of card no & card holder validations
    else if (this.isCheckedAgree == false) {
      let msg = this.toolsProvider.fnLanguageTranslate(["common.error", "view.checkin.error.mustAgree", "buttons.ok"]); // translate of error message
      msg.subscribe((value: string) => {
        let alertnew = this.alertCtrl.create({
          title: value['common.error'],
          message: value["view.checkin.error.mustAgree"],
          cssClass: 'cls_alrt',
          buttons: [
            {
              text: value['buttons.ok'],
              role: 'cancel'
            }]
        });
        alertnew.present();
        setTimeout(() => { alertnew.dismiss(); }, 5000);
      });
    }
    else if (this.card != null && this.card != undefined) {
      this.showLoading();
      let name = this.paymentDataForm.get('cardHolder').value;
      this.stripeService.createToken(this.card, { name }).subscribe(result => {

        if (result.error) {
          let errorMessages = this.toolsProvider.fnLanguageTranslate([result.error.message, "common.error", "buttons.ok"]);
          this.loading.dismiss();
          // Error creating the token
          errorMessages.subscribe((value: string) => {
            let alerterror = this.alertCtrl.create({
              title: value['common.error'],
              message: value[result.error.message],
              cssClass: 'cls_alrt',
              buttons: [
                {
                  text: value['buttons.ok'],
                  role: 'cancel'
                }]
            });
            alerterror.present();
            setTimeout(() => { alerterror.dismiss(); }, 5000);
          });
        }
        else if (result.token) {
          this.checkoutProvider.objCheckOut.Token = result.token.id;
          this.checkoutProvider.objCheckOut.cardExpirationDate = result.token.card.exp_year.toString() + '-' + result.token.card.exp_month.toString();
          this.checkoutProvider.objCheckOut.cardHolder = this.paymentDataForm.controls.cardHolder.value;
          this.checkoutProvider.objCheckOut.cardNumber = null;//this.paymentDataForm.controls.cardNumber.value;
          this.checkoutProvider.objCheckOut.cvv = null;//this.paymentDataForm.controls.cvv.value;
          this.checkoutProvider.objCheckOut.postalCode = result.token.card.address_zip;
          this.checkoutProvider.objCheckOut.guestData = this.storedCheckinDetails != null ? this.storedCheckinDetails.find(item => item.reservationID === this.reservation.objReservation.reservationId).guests : null;
          let msg = this.toolsProvider.fnLanguageTranslate(["common.requestReceived", "view.checkout.success", "common.connectionError", "buttons.ok"]); //  message translation

          this.checkoutProvider.sendCheckOutData().subscribe(data => {
            this.loading.dismiss();
            if (data.success) {
              this.commonProvider.objFormData.checkout = [];
              this.commonProvider.objFormData.checkout.checkoutCompleted = true;
              this.reservation.objReservation.alreadyVerifiedCheckOut = true;
              this.updateLocalStorage();
              this.buttonEnable = false;
              msg.subscribe((value: string) => {
                let alertnew = this.alertCtrl.create({
                  title: value['common.requestReceived'],
                  message: value["view.checkout.success"],
                  cssClass: 'cls_alrt',
                  buttons: [
                    {
                      text: value['buttons.ok'],
                      role: 'cancel',
                      handler: data => {
                        this.navCtrl.push(HomePage, {}, { animate: true, animation: 'transition', duration: 500, direction: 'forward' });
                      }
                    }]
                });
                alertnew.present();
                setTimeout(() => {
                  alertnew.dismiss();
                  this.navCtrl.push(HomePage, {}, { animate: true, animation: 'transition', duration: 500, direction: 'forward' });
                }, 5000);
              });
            }
            else {
              // error message translate

              msg.subscribe((value: string) => {
                let alertnew = this.alertCtrl.create({
                  title: value['common.error'],
                  message: value["common.connectionError"],
                  cssClass: 'cls_alrt',
                  buttons: [
                    {
                      text: value['buttons.ok'],
                      role: 'cancel'
                    }]
                });
                alertnew.present();
                setTimeout(() => { alertnew.dismiss(); }, 5000);
              });
            }
          });
        }
      });
    }
  }

  public showLoading() {
    this.loading = this.loadingCtrl.create({
      spinner: 'crescent'
    });

    this.loading.present();
  }

  //function to handle checking of radio button
  public fnOnRadioSelectAgree($event) {
    this.isCheckedAgree = !this.isCheckedAgree;
  }

  //Navigates to the previous page
  public fnBack() {
    this.navCtrl.pop({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
  }

  getStoredCheckinDetails() {

    this.nativeStorage.getItem('checkinDetails').then(Data => {
      if (Data && Data != undefined) {
        this.storedCheckinDetails = Data;
      }
    });
  }
  public updateLocalStorage() {
    let tempData: any;
    this.nativeStorage.getItem('reservationData').then(reservation => {

      if (reservation != undefined) {
        tempData = reservation;
        tempData.response.huespedYaCheckOutRealizado = true;
        this.nativeStorage.setItem('reservationData', tempData)
          .then(
          () => console.log('Local storage updated.'),
          error => console.error('Error storing item', error)
          );

      }

    });
  }

  ionViewWillLeave() {
    if (this.commonProvider.objFormData && this.commonProvider.objFormData.checkout.checkoutCompleted != true) {
      this.commonProvider.objFormData.checkout.cardHolder = this.paymentDataForm.controls['cardHolder'] && this.paymentDataForm.get('cardHolder').value;
      this.commonProvider.objFormData.checkout.isCheckedAgree = this.isCheckedAgree;
      this.commonProvider.objFormData.checkout.checkoutCompleted = false;
    }
  }

  // fetch pre-entered data into fields from model
  fnCheckPreEnteredFormData() {
    this.commonProvider.objFormData.checkout.checkoutCompleted = false;
    let cardHolder = (this.commonProvider.objFormData.checkout.cardHolder) ? this.commonProvider.objFormData.checkout.cardHolder : this.paymentDataForm.controls['cardHolder'].value;
    let isChecked = (this.commonProvider.objFormData.checkout.isCheckedAgree) ? this.commonProvider.objFormData.checkout.isCheckedAgree : this.isCheckedAgree;

    this.paymentDataForm.controls['cardHolder'] && this.paymentDataForm.get("cardHolder").setValue(cardHolder);
    this.isCheckedAgree = isChecked;
  }

}



